.rating-wrapper {
  margin: auto;
  width: 90%;
  max-width: 600px;
  color: white;
  row-gap: 10px;
  margin-top: 30px;
  text-align: center;
}

.score {
  margin: 12px 0;
  color: #111;
  width: 100%;
  border-style: solid;
  border-width: 1px;
  border-color: #888888;
  padding: 12px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #a1ce6e;
}
.user-info {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 100%;
  margin-left: 30px;
}
