.custon-green-button input {
  font-weight: 500;
  font-family: "Rubik", sans-serif;
  cursor: pointer;
  background-color: rgb(40, 40, 40);
  color: white;
  min-width: 125px;
  margin: 0 20px;
  border-radius: 10px;
  border-style: solid;
  padding: 8px 10px;
  transition: 0.3s;
  border-color: rgb(152, 195, 53);
}

.custon-green-button input:hover {
  color: rgb(40, 40, 40);
  background-color: rgb(152, 195, 53);
  border-color: rgb(152, 195, 53);
}
