@import url("https://fonts.googleapis.com/css2?family=Madimi+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:ital,wght@0,100..900;1,100..900&family=Madimi+One&family=Merriweather+Sans:ital,wght@0,300..800;1,300..800&display=swap");

.header-wrapper {
  background-color: #0f0f0f;
  width: 100%;
  overflow: hidden;
}
.header {
  padding: 5px 0;
  display: flex;
  align-items: center;
  margin-left: 15px;
  text-wrap: nowrap;
}
.hamburger-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 9px;
  width: fit-content;
  transition: 0.1s;
  cursor: pointer;
  border-radius: 50%;
}
.first-hamburger:hover {
  background-color: #272727;
}
.logo-wrapper {
  color: white;
  font-family: "Merriweather Sans", sans-serif;
  font-weight: bold;
  font-size: 20px;
  font-style: normal;
  margin-left: 20px;
}

.overlay {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  display: none;
  z-index: 999;
  background-color: rgb(20, 20, 20, 0.7);
}

.side-bar {
  left: 0;
  top: 0;
  position: fixed;
  background-color: #0f0f0f;
  z-index: 1000;
  height: 100%;
  overflow-x: hidden;
  transition: 0.2s;
}

.navs {
  color: white;
  display: flex;
  flex-wrap: nowrap;
  text-wrap: nowrap;
  flex-direction: column;
  justify-content: center;
  row-gap: 3px;
  margin: 40px 25px;
  font-size: 16px;

  div {
    padding: 8px 12px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    column-gap: 25px;
    cursor: pointer;
  }
  div:hover {
    background-color: #3d3d3d;
  }
}
