.time-modes {
  margin: 30px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  column-gap: 60px;
  row-gap: 30px;
  flex-wrap: wrap;
  align-items: center;
}
.time-mode {
  border-color: #c0de9e;
  border-style: solid;
  padding: 12px 18px;
  color: white;
  border-radius: 5px;
  background-color: #1f1f1f;
  cursor: pointer;
  transition: 0.4s;
  width: max-content;
  text-wrap: nowrap;
}
.time-mode:hover {
  background-color: #3f3f3f;
}
.selected-time {
  border-color: rgb(152, 195, 53);
  background-color: #3f3f3f;
}
.create-room-wrapper {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}
.room-create-wrapper input {
  width: 200px;
}
