* {
  scrollbar-color: #666 #333;
}

.menu-wrapper {
  margin: auto;
  width: 90%;
  max-width: 700px;
}

.rooms-search-box {
  margin-top: 30px;
}
.search-box {
  display: flex;
  justify-content: center;
  width: 100%;
}
.search-box input {
  background-color: #0f0f0f;
  border-style: solid;
  border-color: #3f3f3f;
  color: white;
  width: 80%;
  max-width: 400px;
  padding: 8px 18px;
  font-size: 18px;
  border-radius: 30px;
  border-width: 1.2px;
}
