.profile-wrapper {
  margin: auto;
  text-align: center;
  width: 90%;
  max-width: 700px;
  margin-top: 50px;
  h1 {
    color: white;
    margin-bottom: 30px;
  }
}
.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  margin: 30px 0;
}
.side-text,
.value {
  color: white;
  font-size: 18px;
}
.side-text {
  width: 70px;
}
.value {
  width: 85%;
  background-color: #282828;
  border-color: white;
  border-style: solid;
  border-width: 1px;
  padding: 6px 10px;
  border-radius: 8px;
}
.value:focus {
  border-color: greenyellow;
}
.no-edit {
  pointer-events: none;
}

.edit {
  column-gap: 15px;
}
#edit-password {
  display: none;
  padding-bottom: 50px;
}
.edit {
  display: flex;
  row-gap: 25px;
  flex-wrap: nowrap;
}
@media (max-width: 1200px) {
  .edit {
    flex-direction: column;
  }
}
