.match {
  color: white;
  border-style: solid;
  border-width: 1px;
  border-color: #888888;
  padding: 5px 12px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.won {
  background-color: #82c27e;
}

.lost {
  background-color: #a4443e;
}

.player {
  width: 225px;
}

.timestamp {
  width: 115px;
}

@media (max-width: 610px) {
  .box {
    width: 300px;
  }
}
